import MarkdownIt from 'markdown-it';
import taskLists from 'markdown-it-task-lists';
import { Editor, EditorContent } from 'tiptap';
import { HardBreak, Heading, Code, Link, BulletList, OrderedList, Blockquote, CodeBlock, HorizontalRule, Italic, Strike, ListItem } from 'tiptap-extensions';
export default {
  name: 'PagePreview',
  components: {
    EditorContent
  },
  props: {
    preview: {
      type: Boolean,
      required: true
    },
    edit: {
      type: Boolean,
      required: true
    },
    page: {
      type: Object,
      required: true
    }
  },
  computed: {
    markdownit() {
      return MarkdownIt('commonmark', {
        html: false,
        breaks: false
      }).enable('strikethrough').use(taskLists, {
        enable: true,
        labelAfter: true
      });
    },

    htmlContent() {
      return this.markdownit.render(this.page.content);
    },

    editor() {
      return new Editor({
        editable: false,
        extensions: [new Heading(), new Code(), new Italic(), new Strike(), new HardBreak(), new HorizontalRule(), new BulletList(), new OrderedList(), new Blockquote(), new CodeBlock(), new ListItem(), new Link({
          openOnClick: true
        })],
        content: this.htmlContent
      });
    }

  }
};